import axios from 'axios';
import React from 'react';
//import createHistory from 'history/createBrowserHistory'
import { Form, Icon, Input, Button, Checkbox, notification } from 'antd';
import './login.css';

// const history = createHistory({
//   //forceRefresh: true
// });

const FormItem = Form.Item;

class NormalLoginForm extends React.Component {
  handleSwitchBox = () => {    
    this.props.switchBox('register');
  }

  handleSubmit = (e) => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        //console.log('Received values of form: ', values);

        axios.post('/login', values).then((res) => {
          if(res.data.error == 0) {
            //history.push('/home', { some: 'state'});
            //window.navigate('/home');
            //console.log(window);
            window.location.href="/home";
          } else {
            notification['error']({
            message: '',
              description: res.data.error,
            });

          console.log(res);  
          }
        }).catch(err => {
          notification['error']({
            message: '',
            description: '.',
          });

          console.log(err);
        });
      }
    });
  }

  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Form onSubmit={this.handleSubmit} className="login-form">
        <FormItem>
          {getFieldDecorator('email', {
            rules: [{
              type: 'email', message: '!',
            },{ 
              required: true, message: '!' 
            }],
          })(
            <Input prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />} placeholder="" />
          )}
        </FormItem>
        <FormItem>
          {getFieldDecorator('password_login', {
            rules: [{ required: true, message: '!' }],
          })(
            <Input prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />} type="password" placeholder="" />
          )}
        </FormItem>
        <FormItem>
          {getFieldDecorator('remember', {
            valuePropName: 'checked',
            initialValue: false,
          })(
            <Checkbox></Checkbox>
          )}
          <a className="login-form-forgot" href="javascript:void(0);"></a>
          <Button type="primary" htmlType="submit" className="login-form-button">
            
          </Button>
          <span className="tips-register" ><a href="javascript:void(0);" onClick={this.handleSwitchBox}>!</a></span>
        </FormItem>
      </Form>
    );
  }
}

const WrappedNormalLoginForm = Form.create()(NormalLoginForm);

class KunPengLoginForm extends React.Component {
  render() {
    return (
      <div className="login-box">
        <span className="login-title"></span>
        <span className="login-proverbs"></span>
        <WrappedNormalLoginForm switchBox={this.props.switchBox} />
      </div>
    );  
  }  
}

export { KunPengLoginForm };
